	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p><em>Symbiosis</em> is a collaborative tool for modern urban planning teams to use for designing urban space beneficial to a diverse population. Leveraging data driven process’ and integrating collaborative practices deep within the system design, <em>Symbiosis</em> creates a design environment that fights bias within the urban planning process. Doing so to avoid exclusive space inefficient for a modern society. <em>Symbiosis</em> is a tool for building communities together.</p>\n<p>The full case study will be available at <a href=\"kowalskiforged.com\">kowalskiforged.com</a>.</p>","frontmatter":{"title":"Symbiosis","graduate":"John Kowalski","category":"Product Design","category2":"UI/UX","category3":"Information Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-a7a62.jpg","srcSet":"/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-5a1a9.jpg 375w,\n/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-0908c.jpg 750w,\n/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-a7a62.jpg 1500w,\n/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-bda60.jpg 2250w,\n/the-next-website/static/s_cover-bc34f4ed99e5efea8d749ae2c5547b38-cb414.jpg 3000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/symbiosis/","name":"/Symbiosis/"}}

